@use 'variables';

.signin {

    &_container {
        display: flex;
        padding: 40px 20px;
        box-shadow: 0px 2px 3px 5px #f9f6ef;
        margin: 60px auto;
        border-radius: 10px;

        img {
            width: 100%;
            min-width: 300px;
            max-width: 30vw;
            object-fit: cover;
        }

        @media screen and (max-width:768px) {
            flex-direction: column;

        }
        &-shopify {
            margin: 0 20px;
            width: 100%;
            display: flex;
            flex-direction: column;
            .ButtonUI {
                width: calc(100% - 40px);
                margin: 10px 20px;
            }
        }
    }

    &_text_block {
        display: flex;
        flex-direction: column;
        width: 100%;
        align-items: center;
        justify-content: center;
        gap: 10px;
        form {
            margin: 0 20px;
        }
        button {
            margin: unset;
            width: 100%;
        }
    }

}
.forgot-password {
    text-decoration: none;
    color: variables.$appColor;
}